import React from 'react'
import { StyleSheet, css } from 'aphrodite'
import p from '../asset/img/P.png'

class Item extends React.Component {
  render () {
    return (
      <div>
        <img src={p} className={css(styles.item)} />
      </div>
    )
  }
}

const styles = StyleSheet.create({
  item: {
    width: '75px',
    height: '75px'
  }
})

export default Item


